<template>
	<div>
		<el-card class="box-card">
			<div slot="header" class="clearfix">
				<span></span>
			</div>
			<el-form ref="form" :model="form" label-position="left" label-width="120px">
				<el-form-item label="">
					<el-select v-model="form.cate_1st" @change="cate_1st_handle" placeholder="">
						<el-option v-for="item in cate_1st_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
					</el-select>
					<el-select v-model="form.cate_2nd" @change="cate_2nd_handle" placeholder="">
						<el-option v-for="item in cate_2nd_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
					</el-select>
					<el-select v-model="form.cate_3rd" placeholder="">
						<el-option v-for="item in cate_3rd_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="14">
						<el-input v-model.trim="form.name"></el-input>
					</el-col>
					<el-col :span="24" class="tip">3200</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="14">
						<el-input type="textarea" v-model.trim="form.hotPoint"></el-input>
					</el-col>
					<el-col :span="24" class="tip">140</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model.trim.number="form.price">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0-999999</el-col>
					<el-col :span="24" class="tip"></el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model.trim.number="form.marketPrice">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0-999999</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model.trim.number="form.cost">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0-999999</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input readonly v-model="discount">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip"></el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model.trim.number="form.inventory">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0-999999</el-col>
					<el-col :span="24" class="tip"></el-col>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model.trim="form.articleNo"></el-input>
					</el-col>
					<el-col :span="24" class="tip"></el-col>
					<el-col :span="24" class="tip">20_/-</el-col>
				</el-form-item>
				<el-form-item label="">
					<single-upload action="/api/upload/goods/" :url.sync="form.avatar" />
					<el-upload :limit="1" action="/api/upload/goods/" :file-list="mainFileList" :headers="headers" list-type="picture-card"
					 :before-upload="handleBeforeUpload" :on-success="handleMainSuccess" :before-remove="handleMainBeforeRemove"
					 :on-exceed="handleMainExceed" :on-error="handleError" :on-preview="handleCardPreview">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-col :span="24" class="tip">
						jpgifpng
						800*8001M
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<el-upload :limit="6" action="/api/upload/slider/" :file-list="sliderFileList" :headers="headers" list-type="picture-card"
					 :before-upload="handleBeforeUpload" :on-success="handleSliderSuccess" :on-exceed="handleSliderExceed"
					 :before-remove="handleSliderBeforeRemove" :on-error="handleError" :on-preview="handleCardPreview">
						<i class="el-icon-plus"></i>
					</el-upload>
					<el-col :span="24" class="tip">
						jpgifpng
						800*8001M
					</el-col>
				</el-form-item>
				<div class="section-title"></div>
				<el-form-item label="">
					<el-col :span="14">
						<el-input v-model.trim="form.brand"></el-input>
					</el-col>
				</el-form-item>
				<el-form-item label="">
					<div ref="toolbar" class="w-e-toolbar"></div>
					<div ref="editor" class="w-e-text-container"></div>
				</el-form-item>
				<div class="section-title"></div>
				<el-form-item label="">
					<el-select v-model="form.province" placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
					<el-select v-model="form.city" placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
					<el-select v-model="form.area" placeholder="">
						<el-option label="" value="shanghai"></el-option>
						<el-option label="" value="beijing"></el-option>
						<el-option label="" value="beijing"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-col :span="3">
						<el-input v-model.trim="form.freight">
							<template slot="append"></template>
						</el-input>
					</el-col>
					<el-col :span="24" class="tip">0</el-col>
				</el-form-item>
				<el-form-item>
					<el-button type="success" @click="editHandle"></el-button>
				</el-form-item>
			</el-form>
			<!--  -->
			<el-dialog width="30%" :visible.sync="dialogVisible">
				<img width="100%" :src="dialogImageUrl" alt>
			</el-dialog>
		</el-card>
	</div>
</template>
<script>
	import { Upload, Category, Goods } from "@/api/index";
	import E from "wangeditor";
	
	export default {
		props: ["id"],
		data() {
			return {
				dialogImageUrl: "",
				dialogVisible: false,
				headers: {
					Authorization: `Bearer ${sessionStorage.token}`
				},
				form: {
					cate_1st: "",
					cate_2nd: "",
					cate_3rd: "",
					hotPoint: "",
					name: "",
					price: "",
					marketPrice: "",
					cost: "",
					discount: "",
					inventory: "",
					articleNo: "",
					img_lg: "",
					img_md: "",
					slider: "",
					brand: "",
					detail: "",
					freight: "0",
					province: "",
					city: "",
					area: ""
				},
				cate_1st_options: [],
				cate_2nd_options: [],
				cate_3rd_options: [],
				mainFileList: [],
				sliderFileList: [],
			};
		},
		computed: {
			discount() {
				let discount = ((this.form.price / this.form.marketPrice) * 10).toFixed(2);
				this.form.discount = discount;
				switch (discount) {
					case "NaN":
						return "";
						break;
					case "Infinity":
						return "";
						break;
					default:
						return discount;
						break;
				}
			}
		},
		async mounted() {
			// 
			let data = await this.getDetail();
			//
			const editor = new E(this.$refs.toolbar, this.$refs.editor);
			editor.customConfig.zIndex = 100;
			//
			editor.customConfig.uploadImgServer = "/api/upload/editor/";
			editor.customConfig.uploadFileName = "file";
			// header
			editor.customConfig.uploadImgHeaders = {
				Authorization: `Bearer ${sessionStorage.token}`
			};
			//HTMLdata
			editor.customConfig.onchange = html => {
				this.form.detail = html;
			};
			editor.create();
			editor.txt.html(data.detail);
			// 
			this.cateChangeHandle(1, "cate_1st", false);
			this.cateChangeHandle(data.cate_1st, "cate_2nd", false);
			this.cateChangeHandle(data.cate_2nd, "cate_3rd", false);
			// 
			if (data.img_md) {
				this.mainFileList = [{
					name: '1.jpg',
					url: data.img_md,
				}];
			}
			// 
			let arr = data.slider.split(',');
			let list = arr.map(function(item, index) {
				let obj = {};
				obj.url = item;
				obj.name = index + '.jpg';
				obj.response = { src: item };
				return obj;
			});
			this.sliderFileList = list;
		},
		methods: {
			async getDetail() {
				let { status, data } = await Goods.getDetail({ id: this.id });
				if (status) {
					this.form = data;
					this.form.id = this.id;
					return Promise.resolve(data);
				}
			},
			//change
			cate_1st_handle(id) {
				this.cateChangeHandle(id, "cate_2nd");
			},
			cate_2nd_handle(id) {
				this.cateChangeHandle(id, "cate_3rd");
			},
			async cateChangeHandle(id, cate, setDefault = true) {
				let data = await this.getOptions(id);
				this[cate + "_options"] = data;
				//
				if (data.length === 0) {
					this.form[cate] = "";
					return false;
				}
				if (setDefault) {
					//
					this.form[cate] = data[0].id;
				}
			},
			//
			async getOptions(id) {
				let { status, data, msg } = await Category.load({ pId: id });
				if (status) {
					return Promise.resolve(data);
				} else {
					this.$message.error(msg);
				}
			},
			handleMainSuccess(response, file, fileList) {
				if (response.status) {
					this.form.img_lg = response.lgImg;
					this.form.img_md = response.mdImg;
				}
			},
			handleError(err, file, fileList) {
				let { status, message } = err;
				switch (status) {
					case 401:
						this.$message.error(`:401,Token,!`);
						break;
					case 400:
						message = JSON.parse(message);
						this.$message.error(`:400,${message.msg}`);
						break;
					default:
						this.$message.error(`:${status},${message}!`);
						break;
				}
			},
			handleBeforeUpload(file) {
				let reg = /^image\/(jpe?g|png)$/;
				const isImg = reg.test(file.type);
				const isLt2M = file.size / 1024 / 1024 < 2;
				if (!isImg) {
					this.$message.error(" JPG/PNG !");
				}
				if (!isLt2M) {
					this.$message.error(" 2MB!");
				}
				return isImg && isLt2M;
			},
			async handleMainBeforeRemove(file, fileList) {
				try {
					let res1 = await Upload.remove({ src: "." + this.form.img_lg });
					let res2 = await Upload.remove({ src: "." + this.form.img_md });
					if (res1.status && res2.status) {
						//
						this.form.img_lg = "";
						this.form.img_md = "";
						return true;
					} else {
						return false;
					}
				} catch (e) {
					this.$message.error(e);
				}
			},
			handleCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			handleMainExceed(files, fileList) {
				this.$message.warning(
					` 1  ${files.length}  ${files.length + fileList.length} `
				);
			},
			handleSliderSuccess(response, file, fileList) {
				if (response.status) {
					this.form.slider = this.convertFileList(fileList);
				}
			},
			handleSliderExceed(files, fileList) {
				this.$message.warning(
					` 6  ${files.length}  ${files.length + fileList.length} `
				);
			},
			async handleSliderBeforeRemove(file, fileList) {
				let { status } = await Upload.remove({
					src: "." + file.response.src
				});
				if (status) {
					let i = fileList.findIndex(item => item.url == file.response.src);
					let copy = [...fileList];
					copy.splice(i, 1);
					this.form.slider = this.convertFileList(copy);
					return true;
				} else {
					return false;
				}
			},
			//
			convertFileList(fileList) {
				let res = [];
				fileList.forEach(function(item, index) {
					res.push(item.response.src);
				});
				return res.toString();
			},
			//
			async editHandle() {
				let { status } = await Goods.update(this.form);
				if (status) {
					this.$message.success("");
				}
			}
		}
	};
</script>
<style scoped>
	.tip {
		font-size: 12px;
		color: #999;
	}

	.section-title {
		background-color: #f5f5f5;
		padding: 10px;
		margin-bottom: 10px;
	}

	.el-upload img {
		max-width: 100%;
	}

	.w-e-toolbar {
		border: 1px solid #ccc;
		border-bottom: 0;
	}

	.w-e-text-container {
		border: 1px solid #ccc;
		min-height: 300px;
	}
</style>
