// 0. VueVueRouter Vue.use(Router)
import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)
// 1. import views
// 
import UserLogin from '@/views/User/Login.vue';
import UserRegister from '@/views/User/Register.vue';
import UserList from '@/views/User/List.vue';
import UserInfo from '@/views/User/Info.vue';
// 
import OrderList from '@/views/Order/List.vue';
// 
import Index from '@/views/Index.vue';
// 
import GoodsCategory from '@/views/Goods/Category.vue';
import GoodsList from "@/views/Goods/List";
import GoodsRelease from "@/views/Goods/Release";
import GoodsEdit from "@/views/Goods/Edit";
//
import AuthRole from "@/views/Auth/Role";
import AuthMenu from "@/views/Auth/Menu";
import RoleConfig from "@/views/Auth/Role-Config";

/**
 * 2. 
 * 
 * "component"  Vue.extend() 
 * 
 */
const routes = [
    {
        path: '/register',
        name: 'UserRegister',
        component: UserRegister,
    },
    {
        path: '/login',
        name: 'UserLogin',
        component: UserLogin,
        props: (route) => route.query,
        alias: "/",
    },
    {
        path: '/goods',
        name: 'Goods',
        component: Index,
        redirect: '/goods/list',
        meta: {
            requiredAuth: true
        },
        children: [
            {
                path: 'category',
                name: 'GoodsCategory',
                component: GoodsCategory,
                meta: {
                    requiredAuth: true
                },
            },
            {
                path: "list",
                name: "GoodsList",
                component: GoodsList,
                meta: {
                    requiredAuth: true
                },
            },
            {
                path: "release",
                name: "GoodsRelease",
                component: GoodsRelease,
                meta: {
                    requiredAuth: true
                },
            },
            {
                path: "edit/:id",
                name: "GoodsEdit",
                component: GoodsEdit,
                props: true,
                meta: {
                    requiredAuth: true
                },
            }]
    },
    {
        path: '/user',
        name: 'User',
        component: Index,
        meta: {
            requiredAuth: true
        },
        children: [
            {
                path: "list",
                name: "UserList",
                component: UserList,
                meta: {
                    requiredAuth: true
                },
            }, {
                path: "info",
                name: "UserInfo",
                component: UserInfo,
                meta: {
                    requiredAuth: true
                },
            },
        ]
    },
    {
        path: '/auth',
        name: 'Auth',
        component: Index,
        meta: {
            requiredAuth: true
        },
        children: [
            {
                path: "menu",
                name: "AuthMenu",
                component: AuthMenu,
                meta: {
                    requiredAuth: true
                },
            },
            {
                path: "role",
                name: "AuthRole",
                component: AuthRole,
                meta: {
                    requiredAuth: true
                },
                children: [{
                    path: "config/:id",
                    name: "RoleConfig",
                    component: RoleConfig,
                    props: true,
                    meta: {
                        requiredAuth: true
                    },
                }]
            }]
    },
    {
        path: '/order',
        name: 'Index',
        component: Index,
        meta: {
            requiredAuth: true
        },
        children: [{
            path: "list",
            name: "OrderList",
            component: OrderList,
            meta: {
                requiredAuth: true
            },
        }]
    }
];

// 3.  router  `routes` 
const router = new Router({
    routes // ()  routes: routes
})
// 4.
router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiredAuth)) {
        // token,
        if (!sessionStorage.token) {
            next({
                path: '/login',
                query: {
                    redirect: to.fullPath
                }
            });
            return;
        }
        // 
        next();
    } else {
        next();
    }
});

export default router